import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loading:false,
    token:"",
    userName:"",
    search_list:[],
    search_keywords:[],
    type_flag:true
  },
  mutations: {
    setToken(state,obj){
      state.token=obj.token;
      state.userName=obj.userName;
  },
   
    loading(state,b){
      state.loading=b;
    },
    search_list(state,a){
      state.search_list=a;
     
     
},
update_keywords(state,a){
  if(state.search_keywords.length>8){
        state.search_keywords.shift();
       
  }
  
  if(state.search_keywords.indexOf(a)==-1){
    state.search_keywords.push(a);

  }
  localStorage.setItem("key",JSON.stringify(state.search_keywords))
 
},
type_flag(state,b){
  state.type_flag=b
}
    
  },
  actions: {
  },
  modules: {
  }
})
