import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from "../views/Index"
let Detail=()=>import("../views/Detail")
let Login =()=>import("../views/Login")
let Register=()=>import("../views/Register")
let List=()=>import("../views/List")
let Search_list=()=>import("../views/Search_list")
let ShopCar=()=>import("../views/ShopCar")
Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    component:Index,
    meta:{
      type_flag:true
    },
    name:"首页"
  },{
    path:"/detail/:goodId",
    component:Detail,
    meta:{
      type_flag:true
    },
    name:"详情"
  },{
    path:"/list/:type",
    component:List,
    meta:{
      type_flag:true
    },
    name:"列表"
  },{
    path:"/search_list",
    component:Search_list,
    name:"搜索列表",
    meta:{
      type_flag:true
    },
  },
  {
    path:"/register",
    component:Register,
    meta:{
      type_flag:false
    },
    name:"注册"
  },
  {
    path:"/login",
    component:Login,
    meta:{
      type_flag:false
    },
    name:"登录"
  },
  {
    path:"/shopCar",
    component:ShopCar,
    meta:{
      type_flag:false
    },
    name:"购物车"
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
        return savedPosition
    } else {
        return { x: 0, y: 0 }
    }
}
})

export default router
