<template>
  <div id="app">
    <top_header></top_header>
     <keep-alive>
    <router-view> </router-view>
     </keep-alive>
    <loading></loading>
    <scroll></scroll>
    <bott></bott>
  </div>
</template>

<script>
import loading from "./components/loading"
import scroll from "./components/scroll"
import top_header from "./components/top-header"
import bott from "./components/bott"
export default {
  data(){
        return{
            
        }
    },
  components:{
    loading,
    scroll,
    top_header,
    bott
  },
   created(){
      var res=localStorage.getItem("userInfo");
        if(res){
            res=JSON.parse(res);
            this.$store.commit("setToken",{
                ...res
            })
        }
     this.getsearch()
        // 全局loading显示隐藏
        this.$store.commit("loading",true);
         
        
        this.$router.beforeEach((to,from,next)=>{
        this.$store.commit("type_flag",this.$route.meta.type_flag)
          
            this.$store.commit("loading",true);
         
          
            next();
        })
        this.$router.afterEach(()=>{
            
           
            this.$store.commit("loading",false);
            this.$store.commit("type_flag",this.$route.meta.type_flag)
            
          
        })
    },
    methods:{
      getsearch(){
        var search=JSON.parse(localStorage.getItem("key"))
          if(search){
             this.$store.state.search_keywords=search
               
        }
       
       
      }
    }
}
</script>
