export default function (end = 0, a) {
    var start = document.body.scrollTop || document.documentElement.scrollTop;
    var s = start - end;
    var step = s / 300 * 16.7;
    var timer = setInterval(() => {
        start -= step;
        document.body.scrollTop = start;
        document.documentElement.scrollTop = start;
        // if (s > 0) {
        //     if (start <= end) {
        //         clearInterval(timer);
        //         a ? a() : null
        //     }
        // } else {
        //     if (start >= end) {
        //         clearInterval(timer);
        //         a ? a() : null
        //     }
        // }
        if ((s > 0 && start <= end) || (s < 0 && start >= end)) {
            clearInterval(timer);
            a ? a() : null
        }
    }, 16.7)
}